/* App.css */

@import url('https://fonts.cdnfonts.com/css/mugen-value');

body {
  background-color: rgb(233, 233, 233);
  color: black;
  transition: background-color 0.3s ease, color 0.3s ease;
}

body.dark-mode {
  background-color: rgb(37, 37, 38);
  color: white;
}

.light-nav {
  background-color: #f8f8f8;
  color: #333;
}

.dark-nav {
  color: #fff;
  background-color: #333;
  box-shadow: 0 2px 5px rgba(255, 254, 254, 0.1);
}

nav {
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-invasibility-text {
  color: #f8f8f8;
}

body.dark-mode .nav-invasibility-text {
  color: #333;
}

.nav-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-link {
  margin-right: 20px;
  text-decoration: none;
  background-color: rgba(172, 172, 172, 0.34);
  padding: 8px 18px;
  border-radius: 10px 0 10px 0;
  color: #333;
  font-family: 'MUGEN Value', sans-serif;
  /* font-weight: bold; */
  transition: color 0.3s ease;
}

body.dark-mode .nav-link {
  color: white;
  background-color: rgba(20, 19, 19, 0.34);
}

.nav-link:hover {
  color: #007bff;
}

.theme-toggle {
  padding: 10px;
  color: white;
  background-color: #00000078;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}