.container-article {
    width: 40%;
    background-color: white;
    border-radius: 15px;
    margin: auto;
    margin-top: 40px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.729);
    padding: 10px 25px;
}

body.dark-mode .container-article {
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.729);
    background-color: rgb(26, 25, 25);
}