/* Register.css */

.container {
  max-width: 550px;
  width: 550px;
  margin: 0 auto;
  padding: 20px;
  overflow: auto;
  background-color: #f9f9f9;
  border-radius: 8px;
  user-select: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body.dark-mode .container {
  background-color: #333;
  box-shadow: 0 0 10px #979797;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 28px;
  color: #333;
}

body.dark-mode h2 {
  color: #fff;
}

form {
  display: flex;
  flex-direction: column;
}

div > label {
  margin-bottom: 10px;
  font-size: 16px;
  color: #666;
  display: block;
}

body.dark-mode div > label {
  color: #e7e4e4;
}

input {
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  color: #333;
  background-color: #fff;
  transition: border-color 0.3s, background-color 0.3s;
}

body.dark-mode input {
  color: #ffffff;
  border: 1px solid #1c1a1a;
  background-color: #363434;
}

input:focus {
  outline: none;
  border-color: #6c63ff;
  background-color: #f5f5f5;
}

button {
  padding: 12px 20px;
  background-color: #dedede;
  color: #5a5858;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

body.dark-mode button {
  background-color: #2a2a2c;
  color: #d0d0d0;
}

body.dark-mode button:hover {
  background-color: #292727;
}

button:hover {
  background-color: #d9d3d3;
}

.error {
  color: red;
  margin-top: 10px;
  font-size: 14px;
}

input[type="number"] {
  appearance: textfield;
  -moz-appearance: textfield;
  -webkit-appearance: textfield;
}
