.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 60vh;
  }
  
  h1 {
    font-size: 36px;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 18px;
    margin-bottom: 20px;
  }
  
  .get-started-btn {
    padding: 12px 24px;
    font-size: 16px;
    background-color: #e3e5e7;
    color: #131313;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .get-started-btn:hover {
    background-color: #0056b3;
  }
  
  .language-select {
    padding: 8px;
    font-size: 16px;
    border: none;
    margin: auto;
    margin-bottom: 0%;
    border-radius: 4px;
    width: 60%;
    background-color: #f1eded;
    color: #1c1b1b;
  }
  
  body.dark-mode .language-select {
    background-color: #272424;
    color: #ffffff;
  }
  
  .welcome-text {
     font-weight: bolder;
     margin: auto;
  }

  a {
    color: rgb(71, 71, 222);
  }

  a::after {
    color: #56bade;
  }

  a:focus {
    color: #56bade;
  }